  .product-card:hover .product-card__arrow {
    opacity: 1;
    transform: translateZ(0);
  }
}
@media (max-width: 1296px) {
  .product-card {
    padding: 32px;
    min-height: 300px;
  }
}

.banner-slide {
  @apply bg-gray rounded-primary px-9 pt-[42px] pb-[77px] relative overflow-hidden flex flex-col;
}
@media (max-width: 1296px) {
  .banner-slide {
    @apply pt-6 px-6 pb-14;
  }
}

.complex-card__title {
  margin-top: 24px;
  transition: color 0.22s ease-in-out;
  font-weight: 500;
  font-size: 18px;
  line-height: 1.28;
}
.complex-card__subtitle {
  margin-top: 4px;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.8252;
  color: #8f8f90;
}
@media (min-width: 1297px) {
  .complex-card:hover .complex-card__title {
    color: var(--blue);
  }
}

.product-card-tariff {
  display: flex;
  flex-direction: column;
  padding: 40px 56px 56px;
  background-color: var(--gray);
  position: relative;
  @apply rounded-primary;
}
.product-card-tariff__mode {
  font-weight: 500;
  font-size: 20px;
  line-height: 1.5;
  margin-bottom: 16px;
}
.product-card-tariff__title {
  margin-bottom: 18px;
}
.product-card-tariff__descr {
  margin-bottom: 20px;
}
.product-card-tariff__descr, .product-card-tariff__price-descr {
  font-weight: 500;
  font-size: 16px;
  line-height: 1.3;
  color: #676767;
}
.product-card-tariff__price {
  font-weight: 600;
}
.product-card-tariff__btn {
  margin-top: 63px;
  width: 100%;
}
@media (max-width: 1296px) {
  .product-card-tariff__btn {
    margin-top: 40px;
  }
}
.product-card-tariff__badge {
  position: absolute;
  top: 29px;
  right: 31px;
  padding: 0 16px;
  font-size: 16px;
  line-height: 1.375;
  font-weight: 600;
  color: #2ead56;
  border-radius: 55px;
  height: 34px;
  backdrop-filter: blur(20px);
  background-color: var(--white);
  display: flex;
  align-items: center;
}
.product-card-tariff--profit {
  background: linear-gradient(215deg, #44ca6d 0%, #249e4a 100%);
  color: var(--white);
}
.product-card-tariff--profit .product-card-tariff__descr, .product-card-tariff--profit .product-card-tariff__price-descr {
  color: var(--white);
}
@media (max-width: 1296px) {
  .product-card-tariff {
    padding: 32px;
  }
}